<template>
  <div id="spuVeiw" style="width: 100%; height: 100%;">
    <el-row style="height: 100%">
      <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
      <el-col :span="14" style="height: 100%"><div class="grid-content ep-bg-purple-light"/>

        <!-- 问题类别 -->
        <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.afterResult') }}</span>
            </div>
          </template>
          <div>
            <div class="card-title" v-if="resIs">
              <el-result icon="success"/>
              <div style="margin-left: 10px">
                {{ $t('Clientele.afterSubmitSuccess') }}
                <span class="text item" style="padding-top: 5px"> ({{ $t('Clientele.afterSubmitSuccessButton') }})</span>
              </div>
            </div>

            <div class="card-title" v-if="!resIs">
              <el-result icon="error"/>
              <div style="margin-left: 10px">
                {{ $t('Clientele.afterSubmitError') }}
                <span class="text item" style="padding-top: 5px"> ({{ $t('Clientele.afterSubmitErrorTips') }})</span>
              </div>
            </div>
          </div>

          <div class="text item">{{ $t('Clientele.afterTypeTips') }}</div>
          <el-button round plain size="large" type="primary" class="button-sizes" v-if="resIs" @click="selectAfter()"> {{ $t('common.button.search') }} </el-button>
          <el-button round plain size="large" type="success" class="button-sizes" v-if="resIs" @click="newgo()">{{ $t('common.button.back') }}</el-button>

          <el-button round plain size="large" type="primary" class="button-sizes" v-if="!resIs" @click="errgo()">{{ $t('common.button.back') }}</el-button>
        </el-card>

        <!-- 问题类别 -->
        <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.afterType') }}</span>
            </div>
          </template>
          <div>
            <div class="card-title">
              <el-icon size="40">
                <component :is="problem.icon"/>
              </el-icon>
              <div style="margin-left: 10px">
                {{ problem.title }}
                <span class="text item" style="padding-top: 5px"> ({{ problem.remarks }})</span>
              </div>
            </div>
          </div>
        </el-card>

        <!-- 售后产品 -->
        <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.product') }}</span>
            </div>
          </template>
          <div>
            <div class="card-title">
              <img :src="getUrl(spu.spuImg)" class="spu-img">
              <div style="margin-left: 10px">
                {{ spu.spuName }}
                <br><span class="text item" style="padding-top: 5px"> {{ $t('Clientele.function') }} : {{ spu.functionName }}</span>
                <br><span class="text item" style="padding-top: 5px"> {{ $t('Clientele.series') }}: {{ spu.seriesName }}</span>
              </div>
            </div>
          </div>
        </el-card>

        <!-- 售后工单 -->
        <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.afterOrder') }} - {{afterFrom.afterOrderNumber}}</span>
            </div>
          </template>
          <div class="text item">
            <el-row>
              <el-col :span="2"><div class="grid-content ep-bg-purple" /></el-col>
              <el-col :span="20"><div class="grid-content ep-bg-purple-light" />
                <el-form :model="afterFrom" ref="ruleForm" label-width="100px">
                  <el-form-item :label="$t('Clientele.name')" prop="name">
                    <el-col :span="11">
                      <el-form-item >
                        <el-input v-model="afterFrom.familyName" :readonly="isReadonly" :placeholder="$t('Clientele.namePlaceholder')"/>
                      </el-form-item>
                    </el-col>
                    <el-col :span="13"></el-col>
                  </el-form-item>
                  <el-form-item prop="email" :label="$t('Clientele.email')">
                    <el-input v-model="afterFrom.email" :readonly="isReadonly" :placeholder="$t('Clientele.emailPlaceholder')"></el-input>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.spuSequence')">
                    <el-input v-model="afterFrom.spuSequence" :readonly="isReadonly" :placeholder="$t('Clientele.spuSequencePlaceholder')"/>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.buyTime')">
                    <el-date-picker v-model="afterFrom.buyTime" :disabled="isReadonly" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD" :placeholder="$t('Clientele.buyTimePlaceholder')" style="width: 46%"/>
                  </el-form-item>

                  <el-form-item :label="$t('Clientele.buyDitchName')">
                    <el-col :span="11">
                      <el-form-item >
                        <el-select filterable v-model="afterFrom.buyDitch" :disabled="isReadonly"  :placeholder="$t('Clientele.buyDitchNamePlaceholder')" style="width: 100%">
                          <el-option v-for="item in pullDownData.ditchList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
                        </el-select>
                      </el-form-item>
                    </el-col>
                    <el-col :span="2" class="text-center"><el-icon><Minus /></el-icon></el-col>
                    <el-col :span="11">
                      <el-form-item >
                        <el-input v-model="afterFrom.ditchRemark" :readonly="isReadonly" :placeholder="$t('Clientele.ditchRemarksPlaceholder')"/>
                      </el-form-item>
                    </el-col>
                  </el-form-item>

                  <el-form-item :label="$t('Clientele.region')" >
                    <el-select v-model="afterFrom.region" :disabled="isReadonly"  filterable :placeholder="$t('Clientele.regionPlaceholder')" style="width: 46%">
                      <el-option v-for="item in pullDownData.regionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.summary')" >
                    <el-input v-model="afterFrom.summary" :readonly="isReadonly" :placeholder="$t('Clientele.summaryPlaceholder')"/>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.detail')" >
                    <el-input v-model="afterFrom.detail" :readonly="isReadonly" :rows="3" type="textarea" :placeholder="$t('Clientele.detailPlaceholder')"/>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.prove')">
                    <el-input v-model="afterFrom.prove" :readonly="isReadonly" :placeholder="$t('Clientele.provePlaceholder')"/>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.videoUrl')">
                    <el-input v-model="afterFrom.vedioUrl" :readonly="isReadonly" :placeholder="$t('Clientele.videoUrlPlaceholder')"/>
                  </el-form-item>
                </el-form>
              </el-col>
              <el-col :span="2"><div class="grid-content ep-bg-purple" /></el-col>
            </el-row>
          </div>
        </el-card>

      </el-col>
      <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
    </el-row>
  </div>
</template>

<script>

import {getDictPullDownClientele, getRegionPullDown} from "@/http/clienteleApi";

export default {

  name: "spuVeiw",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      resIs: true,
      // 表单下拉数据
      pullDownData: {},
      isReadonly: true,

      // 售后类型
      problem: {},
      // 产品数据
      spu: {},
      // 表单数据
      afterFrom: {},
      webUrl: '',
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },
  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },
    getSData() {
      this.webUrl = process.env.VUE_APP_BACKEND_WEB_URL + "/selectAfter/" + this.afterFrom.selcetUrl;
      // 获取下拉数据
      this.getPullDownData();
    },
    // 获取表单下拉数据
    getPullDownData() {
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.pullDownData.regionList = res.data.data;
      });
      // 渠道
      getDictPullDownClientele({dictCode: 'GMQD'}).then(res => {
        if (res.data.status == 200) this.pullDownData.ditchList = res.data.data;
      });
    },

    selectAfter() {
      // 路由跳转
      this.$router.push('/selectAfter/' + this.afterFrom.selcetUrl);
    },

    // 出错按钮的回调 返回tab0
    errgo() {
      this.res = {};
      this.spu = {};
      this.$emit('childClick', this.resIs);
    },
    newgo() {
      this.res = {};
      this.spu = {};
      this.$emit('childClick', false);
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
/*图标*/
.el-result {
  padding: 10px;
}
.button-sizes {
  width: 100px;
  height: 50px;
}


/* 卡片 */
.text-center {
  text-align: center;
}
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-button {
  color: #409eff;
  border: 1px solid rgb(64, 158, 255, 0);
}
.card-button:hover {
  /*下划线*/
  text-decoration: underline;
  border: 1px solid #409eff;
}
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}

.box-card {
  border: 1px solid rgba(0, 0, 0, 0.1);
  /*圆角*/
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s;
  cursor: pointer;
  /*背景色*/
  background-color: #f5f5f7;
  margin-bottom: 20px;
}
.box-card:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  border: 1px solid rgb(64, 158, 255, 0.8);
  /*color: rgb(64, 158, 255, 0.8);*/
}
.card-title {
  font-size: 20px;
  /*font-weight: bold;*/
  color: #092441;
  display: flex;
  /*justify-content: center;*/
  align-items: center;
}

.spu-img {
  height: 80px;
  width: 80px;
}
</style>
