          </el-card>
        </div>

        <el-divider>
          <el-icon><Link /></el-icon>
        </el-divider>

        <div class="card">
          <el-card class="box-card" v-for="problem in problemList" :key="problem.id" @click="nextStep(problem)">
            <div class="card-title">
              <el-icon size="45">
                <component :is="problem.icon"/>
              </el-icon>
              <div style="margin-left: 10px">{{ problem.title }}</div>
            </div>
            <div class="text item" style="padding-top: 5px">
              {{ problem.remarks }}
            </div>
          </el-card>
        </div>
      </el-col>
      <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
    </el-row>
  </div>
</template>

<script>
import {listProblemClientele} from "@/http/clienteleApi";

export default {

  name: "problemVeiw",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      problemList: [],
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getViewData();
  },
  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getViewData() {
      listProblemClientele().then(res => {
        if (res.data.status == 200) this.problemList = res.data.data;
      });
    },
    nextStep(problem) {
      this.$emit('childClick', problem);
    },
  },
}
</script>

