<template>
  <div id="clienteleVeiw" class="clienteleVeiw" >
    <div class="common-layout">
      <el-container>
        <el-header class="header">
          <el-row :gutter="20" style="height: 100%">
            <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
            <el-col :span="14"><div class="grid-content ep-bg-purple" />
              <div class="headerBox">
<!--                <el-button type="primary" plain @click="active = 3">步骤四</el-button>-->
<!--                <span>{{ $t('Clientele.header') }}</span>-->
                <div><img src="../../assets/ifi-logo.png" alt="ifiLogo" style="width: 30px;"></div>
                <div style="margin-left: 10px">{{ $t('Clientele.header') }}</div>
                <div class="linkBox">
                  <!-- 主题_选项 -->
                  <el-switch
                      class="themeBox"
                      v-model="theme"
                      active-action-icon="Watermelon"
                      inactive-action-icon="MagicStick"
                      @change="themeSwitchChange()"
                  />
                  <el-dropdown @command="switchover" >
                  <span class="el-dropdown-link">
                    <svg class="linkSvg" preserveAspectRatio="xMidYMid meet" viewBox="0 0 24 24" width="1.2em" height="1.2em" data-v-12008bb2=""><path fill="currentColor" d="m18.5 10l4.4 11h-2.155l-1.201-3h-4.09l-1.199 3h-2.154L16.5 10h2zM10 2v2h6v2h-1.968a18.222 18.222 0 0 1-3.62 6.301a14.864 14.864 0 0 0 2.336 1.707l-.751 1.878A17.015 17.015 0 0 1 9 13.725a16.676 16.676 0 0 1-6.201 3.548l-.536-1.929a14.7 14.7 0 0 0 5.327-3.042A18.078 18.078 0 0 1 4.767 8h2.24A16.032 16.032 0 0 0 9 10.877a16.165 16.165 0 0 0 2.91-4.876L2 6V4h6V2h2zm7.5 10.885L16.253 16h2.492L17.5 12.885z"></path></svg>
                  </span>
                    <template #dropdown>
                      <el-dropdown-menu>
                        <el-dropdown-item command="zhCn">
                          <el-icon><Football /></el-icon> {{ $t("HomeView.language.zh") }}
                        </el-dropdown-item>
                        <el-dropdown-item command="en">
                          <el-icon><Help /></el-icon> {{ $t("HomeView.language.en") }}
                        </el-dropdown-item>
                      </el-dropdown-menu>
                    </template>
                  </el-dropdown>
                </div>
              </div>
            </el-col>
            <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
          </el-row>
        </el-header>

        <el-main class="main">
          <problemVeiw v-show="active == 0" ref="problem" @childClick="setProblem"/>
          <spuVeiw v-show="active == 1" ref="spuInfo" @childClick="spuVeiw"/>
          <fromVeiw v-show="active == 2" ref="fromInfo" @childClick="fromInfo"/>
          <resultVeiw v-show="active == 3" ref="result" @childClick="result"/>
        </el-main>

        <el-footer>
          <div class="footer">
            <div class="scroll-container">
              <div v-if="MessagePushList.length > 0" class="scroll-item" :key="currentIndex">
                <span style="font-weight: bold; color: #dc2c2c">
                  {{ MessagePushList[currentIndex].title }}
                </span>
                :
                <span style="color: #858181">
                  {{ MessagePushList[currentIndex].content }}
                </span>
              </div>
            </div>
          </div>
        </el-footer>
      </el-container>
    </div>
  </div>
</template>

<script>
import problemVeiw from "@/clientView/after_from/problemVeiw";
import spuVeiw from "@/clientView/after_from/spuVeiw";
import fromVeiw from "@/clientView/after_from/fromVeiw";
import resultVeiw from "@/clientView/after_from/resultVeiw";
import {listMessagePushClientele} from "@/http/clienteleApi";
import en from 'element-plus/dist/locale/en.mjs'
import zhCn from 'element-plus/dist/locale/zh-cn.mjs'
import {getSysTheme} from "@/util/system";

export default {

  name: "clienteleVeiw",
  components: {problemVeiw, spuVeiw, fromVeiw, resultVeiw},
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      theme: false,
      // 步骤条
      active: 0,
      // 页面数据
      sfterData: {},

      // 售后类型
      problem: {},
      // 产品数据
      spu: {},
      // 售后工单数据
      afterFrom: {},

      MessagePushList: [], // 消息推送列表
      currentIndex: 0, // 当前消息推送索引
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    this.getViewData();
    this.startAutoScroll();
    this.setTheme();
  },
  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getViewData() {
      listMessagePushClientele().then(res => {
        if (res.data.status == 200) {
          this.MessagePushList = res.data.data;
        }
      }).catch((erro) => { this.reset(); });
    },
    startAutoScroll() {
      setInterval(() => {
        if (this.currentIndex == this.MessagePushList.length - 1) {
          this.currentIndex = 0;
        } else {
          this.currentIndex++;
        }
      }, 5000);
    },
    // 本地主题开关
    setTheme() {
      this.theme = getSysTheme();
    },
    // 主题开关
    themeSwitchChange() {
      // theme 主题开关 true=暗黑 false=明亮
      window.localStorage.setItem("themeSwitch", this.theme);
      // 调用父组件的方法
      this.$emit('childClick', 'theme');
      // 刷新网页
      location.reload();
    },
    // 子组件调用父组件的方法 切换语言 值必须是语言文件的引入名称
    switchover(command) {
      if (command == 'en') {
        this.$confirm(this.$t("HomeView.languageDialog.enContent"), this.$t("HomeView.languageDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'success'
        }).then(() => {
          this.gotolanguage('en');
        });
      } else {
        this.$confirm(this.$t("HomeView.languageDialog.zhContent"), this.$t("HomeView.languageDialog.title"), {
          confirmButtonText: this.$t("common.button.confirm"),
          cancelButtonText: this.$t("common.button.cancel"),
          type: 'success'
        }).then(() => {
          this.gotolanguage('zh');
        });
      }
    },
    // 切换语言 刷新首页框架数据 重新获取菜单 重新获取标签 重新获取标题 ...
    gotolanguage(locale) {
      this.$i18n.locale = locale; // 切换 I18n 语言包 选项式写法
      if (locale == 'en') {
        this.$emit('childClick', en);
        window.localStorage.setItem("navigatorLanguage", 'en-US');
      } else if (locale == 'zh'){
        this.$emit('childClick', zhCn);
        window.localStorage.setItem("navigatorLanguage", 'zh-CN');
      }
      // 刷新网页
      location.reload();
    },

    // --------------------------------------------------------------------
    // 页面一: 选择售后类型 / 切换回调
    setProblem(problem) {
      // this.sfterData.problemId = problem.id;
      this.problem = problem;
      this.active = 1;
      this.$refs.spuInfo.problem = problem;
      this.$refs.spuInfo.getSpuData();
    },
    // 页面二: 产品选择 / 切换回调
    spuVeiw(spu) {
      if (spu == null) {
        this.active = 0;
        return;
      }
      this.spu = spu;
      // this.sfterData.spu = spu;
      // this.sfterData.spuId = spu.id;
      this.active = 2;
      // 延迟设置属性，确保组件已经准备好
      this.$nextTick(() => {
        if (this.$refs.fromInfo) {
          this.$refs.fromInfo.problem = this.problem;
          this.$refs.fromInfo.spu = this.spu;
          this.$refs.fromInfo.afterFrom.problemId = this.sfterData.problemId;
          this.$refs.fromInfo.getSData();
        }
      }, 200);
    },
    // 页面三: 表单提交 / 切换回调
    fromInfo(resData) {
      if (resData.res == 0) {
        this.$refs.result.res = false;
        return
      }
      if (resData.res == 2) {
        this.active = 0;
        return;
      }
      if (resData.res == 3) {
        this.setProblem(this.problem);
        return;
      }
      if (resData.res == 1) {
        this.afterFrom = resData.afterFrom;
        this.active = 3;
        // 延迟设置属性，确保组件已经准备好
        if (!this.$refs.result) {
          return;
        }
        this.$nextTick(() => {
          this.$refs.result.resIs = true;
          this.$refs.result.spu = this.spu;
          this.$refs.result.problem = this.problem;
          this.$refs.result.afterFrom = this.afterFrom;
          console.log(this.afterFrom, 'this.afterFrom')
          this.$refs.result.getSData();
        }, 200);
      }
    },
    // 页面四: 按钮 / 切换回调
    result(reis) {
      if (reis) {

      }  else {
        this.active = 0;
      }
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.clienteleVeiw {
  height: 100%;
}
.common-layout{
  height: 100%;
}
.main {
  height: 86vh;
  /*border: 1px solid #000;*/
  background-color: #ffffff;
}
/* 页面顶部*/
.headerBox {
  height: 100%;
  width: 100%;
  font-size: 20px;
  font-weight: bold;
  color: #092441;
  display: flex;
  /*justify-content: center;*/
  align-items: center;
  position: relative;
}
.linkBox{
  position: absolute;
  right: 0px;
  display: flex;
  float: right;
  align-items: center;
}
/* 页面底部 */
.footer {
  /* 内部元素居中 */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  /*background-color: #1f1f1f;*/
}
/* 页面顶部*/
.header {
  height: 60px;
  border-bottom: 1px solid rgb(212, 219, 225, 0.3);
}

/* 通知栏样式 */
.scroll-container {
  overflow: hidden;
  height: 25px; /* 调整你希望显示的高度 */
}

.scroll-item {
  animation: scrollAnimation 7s infinite;
}
@keyframes scrollAnimation {
  0% {
    transform: translateY(100%);
  }
  50% {
    transform: translateY(0);
  }
  100% {
    transform: translateY(-100%);
  }
}

.view {
  display: flex;
  width: 100%;
  height: 90%;
  overflow: hidden; /* 隐藏溢出部分 */
}
.viewBox {
  flex: 0 0 100%; /* 设置每个 .viewBox 的宽度为100% */
  height: 100%;
  box-sizing: border-box;
  transition: transform 0.3s ease; /* 添加过渡效果，0.3秒的过渡时间，使用ease缓动函数 */
}

.linkSvg{
  color: #1a1a1a;
  width: 25px;
  height: 25px;
}
.themeBox{
  margin-right: 10px;
}
</style>
