<template>
  <div id="spuVeiw" style="width: 100%; height: 100%;">

    <el-row style="height: 100%">
      <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
      <el-col :span="14" style="height: 100%"><div class="grid-content ep-bg-purple-light"/>

        <!-- 问题类别 -->
        <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.afterType') }}</span>
              <el-button class="card-button" text @click="goEmits(2)">{{ $t('common.button.change') }}</el-button>
            </div>
          </template>
          <div>
            <div class="card-title">
              <el-icon size="40">
                <component :is="problem.icon"/>
              </el-icon>
              <div style="margin-left: 10px">
                {{ problem.title }}
                <span class="text item" style="padding-top: 5px"> ({{ problem.remarks }})</span>
              </div>
            </div>
          </div>
        </el-card>

        <!-- 售后产品 -->
        <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.product') }}</span>
              <el-button class="card-button" text @click="goEmits(3)">{{ $t('common.button.change') }}</el-button>
            </div>
          </template>
          <div>
            <div class="card-title">
              <img :src="getUrl(spu.spuImg)" class="spu-img">
              <div style="margin-left: 10px">
                {{ spu.spuName }}
                <br><span class="text item" style="padding-top: 5px"> {{ $t('Clientele.function') }} : {{ spu.functionName }}</span>
                <br><span class="text item" style="padding-top: 5px"> {{ $t('Clientele.series') }}: {{ spu.seriesName }}</span>
              </div>
            </div>
          </div>
        </el-card>

        <!-- 售后工单 -->
        <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.afterOrder') }}</span>
              <el-button class="card-button" text @click="reset()">{{ $t('common.button.reset') }}</el-button>
            </div>
          </template>
          <div class="text item">
            <el-row>
              <el-col :span="2"><div class="grid-content ep-bg-purple" /></el-col>
              <el-col :span="20"><div class="grid-content ep-bg-purple-light" />
                <el-form :model="afterFrom" :rules="afterFromVerify" ref="ruleForm" label-width="100px">
                  <el-form-item :label="$t('Clientele.name')" prop="name">
                    <el-col :span="11">
                      <el-form-item prop="name">
                        <el-input v-model="afterFrom.name" :placeholder="$t('Clientele.namePlaceholder')"/>
                      </el-form-item>
                    </el-col>
                    <el-col :span="2" class="text-center"><el-icon><Minus /></el-icon></el-col>
                    <el-col :span="11">
                      <el-form-item prop="family">
                        <el-input v-model="afterFrom.family" :placeholder="$t('Clientele.familyNamePlaceholder')"/>
                      </el-form-item>
                    </el-col>
                  </el-form-item>
                  <el-form-item prop="email" :label="$t('Clientele.email')">
                    <el-input v-model="afterFrom.email" :placeholder="$t('Clientele.emailPlaceholder')"></el-input>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.spuSequence')" prop="spuSequence">
                    <el-input v-model="afterFrom.spuSequence"  :placeholder="$t('Clientele.spuSequencePlaceholder')"/>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.buyTime')" prop="buyTime">
                    <el-date-picker v-model="afterFrom.buyTime" type="date" format="YYYY-MM-DD" value-format="YYYY-MM-DD" :placeholder="$t('Clientele.buyTimePlaceholder')" style="width: 46%"/>
                  </el-form-item>

                  <el-form-item :label="$t('Clientele.buyDitchName')" required>
                    <el-col :span="11">
                      <el-form-item prop="buyDitch">
                        <el-select filterable v-model="afterFrom.buyDitch" :placeholder="$t('Clientele.buyDitchNamePlaceholder')" style="width: 100%">
                          <el-option v-for="item in pullDownData.ditchList" :key="item.dictValue" :label="item.dictKey" :value="item.dictValue"></el-option>
                        </el-select>
                      </el-form-item>
                    </el-col>
                    <el-col :span="2" class="text-center"><el-icon><Minus /></el-icon></el-col>
                    <el-col :span="11">
                      <el-form-item prop="ditchRemark">
                        <el-input v-model="afterFrom.ditchRemark" :placeholder="$t('Clientele.ditchRemarksPlaceholder')"/>
                      </el-form-item>
                    </el-col>
                  </el-form-item>

                  <el-form-item :label="$t('Clientele.region')" prop="region">
                    <el-select v-model="afterFrom.region" filterable :placeholder="$t('Clientele.regionPlaceholder')" style="width: 46%">
                      <el-option v-for="item in pullDownData.regionList" :key="item.id" :label="item.name" :value="item.id"></el-option>
                    </el-select>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.summary')" prop="summary">
                    <el-input v-model="afterFrom.summary" :placeholder="$t('Clientele.summaryPlaceholder')"/>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.detail')" prop="detail">
                    <el-input v-model="afterFrom.detail" :rows="3" type="textarea" :placeholder="$t('Clientele.detailPlaceholder')"/>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.prove')">
                    <el-input v-model="afterFrom.prove" :placeholder="$t('Clientele.provePlaceholder')"/>
                  </el-form-item>
                  <el-form-item :label="$t('Clientele.videoUrl')">
                    <el-input v-model="afterFrom.vedioUrl" :placeholder="$t('Clientele.videoUrlPlaceholder')"/>
                  </el-form-item>

                  <el-form-item :label="$t('Clientele.andCheck')"  prop="type">
                    <el-checkbox-group v-model="afterFrom.type">
                      <el-checkbox :label="$t('pullDown.afterFromType.privacy')" name="type" />
                      <el-checkbox :label="$t('pullDown.afterFromType.disclaimer')" name="type" />
                      <el-checkbox :label="$t('pullDown.afterFromType.terms')" name="type" />
                    </el-checkbox-group>
                  </el-form-item>

<!--                  <el-form-item></el-form-item>-->
                </el-form>
              </el-col>
              <el-col :span="2"><div class="grid-content ep-bg-purple" /></el-col>
            </el-row>
          </div>
        </el-card>

        <el-card class="box-card">
          <div class="text item">{{ $t('Clientele.afterTypeTips') }}</div>
<!--          <el-button round size="large" style="width: 100px; height: 50px" type="primary" @click="addAfter()"> {{ $t('common.button.confirm') }} </el-button>-->
          <el-button round plain size="large" style="width: 100px; height: 50px" type="primary" @click="addAfter()"> {{ $t('common.button.continue') }} </el-button>
        </el-card>



      </el-col>
      <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
    </el-row>
  </div>
</template>

<script>
import {getDictPullDownClientele, getRegionPullDown, saveAfterClientele} from "@/http/clienteleApi";
import {ElLoading} from "element-plus";

export default {

  name: "spuVeiw",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      // 表单下拉数据
      pullDownData: {},

      // 售后类型
      problem: {},
      // 产品数据
      spu: {},

      // 表单数据
      afterFrom: {},
      // 返回数据结果
      result: {
        res: 0, // 0: 失败 1: 成功 2: 更改售后类型 3: 更改产品
        afterFrom: {} // 售后工单数据
      },
      // 表单验证
      afterFromVerify: {
        name: [
          { required: true, message: this.$t('Clientele.namePlaceholder'), trigger: 'blur' },
        ],
        family: [
          { required: true, message: this.$t('Clientele.familyNamePlaceholder'), trigger: 'blur' },
        ],
        email: [
          { required: true, message: this.$t('Clientele.emailPlaceholder'), trigger: 'blur' },
          { type: 'email', message: this.$t('Clientele.emailError'), trigger: ['blur', 'change'] }
        ],
        spuSequence: [
          { required: true, message: this.$t('Clientele.spuSequencePlaceholder'), trigger: 'blur' },
        ],
        buyTime: [
          { required: true, message: this.$t('Clientele.buyTimePlaceholder'), trigger: 'blur' },
        ],
        buyDitch: [
          { required: true, message: this.$t('Clientele.buyDitchNamePlaceholder'), trigger: 'change' }
        ],
        ditchRemark: [
          { required: true, message: this.$t('Clientele.ditchRemarksPlaceholder'), trigger: 'blur' }
        ],
        region: [
          { required: true, message: this.$t('Clientele.regionPlaceholder'), trigger: 'change' }
        ],
        summary: [
          { required: true, message: this.$t('Clientele.summaryPlaceholder'), trigger: 'blur' },
        ],
        detail: [
          { required: true, message: this.$t('Clientele.detailPlaceholder'), trigger: 'blur' },
        ],
        type: [
          { type: 'array', required: true, message: this.$t('Clientele.check'), trigger: 'change' },
          {
            validator: (rule, value, callback) => {
              if (value.length != 3) {
                callback(new Error(this.$t('Clientele.check')))
              } else {
                callback()
              }
            },
            trigger: 'change'
          }
        ],
      },
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {
    // this.getSData();
  },
  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },
    // 获取表单下拉数据
    getPullDownData() {
      // 地区 暂测
      getRegionPullDown().then(res => {
        if (res.data.status == 200) this.pullDownData.regionList = res.data.data;
      }).catch((erro) => { });
      // 渠道
      getDictPullDownClientele({dictCode: 'GMQD'}).then(res => {
        if (res.data.status == 200) this.pullDownData.ditchList = res.data.data;
      }).catch((erro) => { });
    },
    getSData() {
      // 获取下拉数据
      this.getPullDownData();
    },
    reset() {
      this.$refs.ruleForm.resetFields(); // 重置表单验证
      this.afterFrom={}
    },
    addAfter() {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          // 通过验证进行提交 保存数据 / 加载动画
          const loading = ElLoading.service({
            lock: true,
            text: this.$t('common.loadingText'),
            background: 'rgba(0, 0, 0, 0.7)',
          })
          this.afterFrom.problemId = this.problem.id;
          this.afterFrom.spu = this.spu;
          // 提交表单
          this.afterFrom.webUrl = process.env.VUE_APP_BACKEND_WEB_URL + "/selectAfter/";
          saveAfterClientele(this.afterFrom).then(res => {
            if (res.data.status == 200) {
              window.localStorage.setItem('addAfterOrderNumberClientele', res.data.data.selcetUrl);
              this.result.afterFrom = res.data.data;
              this.goEmits(1);
            } else {
              this.goEmits(0);
            }
            this.afterFrom = {};
            this.spu = {};
            this.$refs.ruleForm.resetFields(); // 重置表单验证
            loading.close();
          }).catch((erro) => {
            loading.close()
            this.$emit('childClick', null)
          });
        } else {
          return false;
        }
      });
    },
    // 返回父组件
    goEmits(res) {
      this.result.res = res;
      this.$emit('childClick', this.result);
    },
  },

}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}
/*表单卡片*/
.text-center {
  text-align: center;
}

/* new 卡片 */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-button {
  color: #409eff;
  border: 1px solid rgb(64, 158, 255, 0);
}
.card-button:hover {
  /*下划线*/
  text-decoration: underline;
  border: 1px solid #409eff;
}
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}

.box-card {
  border: 1px solid rgba(0, 0, 0, 0.1);
  /*圆角*/
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s;
  cursor: pointer;
  /*背景色*/
  background-color: #f5f5f7;
  margin-bottom: 20px;
}
.box-card:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  border: 1px solid rgb(64, 158, 255, 0.8);
  /*color: rgb(64, 158, 255, 0.8);*/
}
.card-title {
  font-size: 20px;
  /*font-weight: bold;*/
  color: #092441;
  display: flex;
  /*justify-content: center;*/
  align-items: center;
}

/* 产品 样式 */
.spu-img {
  height: 80px;
  width: 80px;
}

.button-card {
  display: flex;
  /*justify-content: center;*/
  align-items: center;
  height: 100px;
  width: 100%;
  margin-top: 20px;
  background-color: #f5f5f7;
}
</style>
