<template>
  <div id="spuVeiw" style="width: 100%; height: 100%;">

    <!-- 背景 -->
    <div class="spu-back">
      <el-row style="height: 100%">
        <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
        <el-col :span="14" style="height: 100%"><div class="grid-content ep-bg-purple-light"/>
          <div class="spu-title">{{ $t('Clientele.help') }}</div>
          <div>
            {{ $t('Clientele.contact') }}
          </div>
        </el-col>
        <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
      </el-row>
    </div>

    <el-row style="height: 100%">
      <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
      <el-col :span="14" style="height: 100%"><div class="grid-content ep-bg-purple-light"/>

        <!-- 问题类别 -->
        <el-card class="box-card">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.afterType') }}</span>
              <el-button class="card-button" text @click="spuNextStep(null)">
                {{ $t('common.button.change') }}
              </el-button>
            </div>
          </template>
          <div>
            <div class="card-title">
              <el-icon size="40">
                <component :is="problem.icon"/>
              </el-icon>
              <div style="margin-left: 10px">
                {{ problem.title }}
                <div class="text item" style="padding-top: 5px">{{ problem.remarks }}</div>
              </div>
            </div>
          </div>
        </el-card>

        <!-- 选择产品 -->
        <el-card class="box-card" style="margin-top: 15px">
          <template #header>
            <div class="card-header">
              <span>{{ $t('Clientele.selectProduct') }}</span>
            </div>
          </template>
          <div>
            <el-tabs tab-position="left" style="width: 100%; height: 100%;" v-model="activeTab" @tab-click="handleTabClick">
              <el-tab-pane v-for="fun in functionList" :key="fun.id" :label="fun.name" style="width: 100%; height: 100%; overflow: auto;" >
                <div style="max-height: 700px; width: 100%; overflow: auto;">

                  <el-card class="spu-card" v-for="spu in spuList" :key="spu.id" shadow="never" @click="spuNextStep(spu)">
                    <div class="card-title">
                      <img :src="getUrl(spu.spuImg)" class="spu-img">
                      <div style="margin-left: 10px">
                        {{ spu.spuName }}
                      </div>
                    </div>
                  </el-card>
                </div>
              </el-tab-pane>
            </el-tabs>
          </div>
        </el-card>

      </el-col>
      <el-col :span="5"><div class="grid-content ep-bg-purple" /></el-col>
    </el-row>
  </div>
</template>

<script>
import {getFunctionClientele, getSpuByFunctionClientele} from "@/http/clienteleApi";

export default {

  name: "spuVeiw",
  // data: 对象就是要渲染到页面上的数据
  data: function () {
    return {
      activeTab: '0',

      problem: {},
      functionList: [],
      spuList: [],
    }
  },

  // moubted: 页面加载完毕后执行
  mounted() {

  },
  // methods: 对象中的方法，可以在页面中调用
  methods: {
    getUrl(photoUrl) {
      return process.env.VUE_APP_BACKEND_API_URL + photoUrl;
    },
    getSpuData() {
      this.activeTab = '0',
      getFunctionClientele().then(res => {
        if (res.data.status == 200) {
          this.functionList = res.data.data;
          // 根据产品功能获取产品列表
          this.getSpuByFunctionId(this.functionList[0]);
        }
      });
    },
    // 根据产品功能获取产品列表
    getSpuByFunctionId(item) {
      getSpuByFunctionClientele({functionId: item.id}).then(res => {
        if (res.data.status == 200) {
          this.spuList = res.data.data;
        }
      }).catch((erro) => { });
    },
    handleTabClick(tab) {
      let entity = this.functionList[tab.paneName];
      this.getSpuByFunctionId(entity);
    },

    // 选择产品之后切换回调
    spuNextStep(spu) {
      this.$emit('childClick', spu);
    },
  },
}
</script>

<!--组件css样式 scoped:表示该样式只在此组件生效-->
<style scoped>
.time {
  height: 30px;
  font-size: 12px;
  color: #999;
  overflow: hidden;
}
.bottom {
  margin-top: 13px;
  line-height: 12px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.button {
  padding: 0;
  min-height: auto;
}
.image {
  width: 100%;
  height: 100%;
  object-fit: cover
  /*display: block;*/
}
.el-tab-pane {
  width: 100%;
  height: 100%;
}
::-webkit-scrollbar {
  display: none; /* Chrome Safari */
}
.spubox{
  height: 260px;
  width: 24%;
  margin-bottom: 15px;
  margin-right: 3px;
  float: left;
}

.imgbox{
  height: 145px;
  width: 99%;
  border-bottom: 10px;
  /*border: 1px #0d568a solid;*/
}

.spu-back {
  width: 100%;
  height: 345px;
  background: url(../../assets/beijing.png) no-repeat;
  background-size: 100% 100%;
  z-index: -1;
  margin-bottom: 20px;
  border-bottom: 1px solid rgba(94, 95, 96, 0.5);
}
.spu-title{
  font-size: 30px;
  margin-top: 100px;
  margin-bottom: 20px;
  font-weight: bold;
}

/* 卡片 */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.card-button {
  color: #409eff;
  border: 1px solid rgb(64, 158, 255, 0);
}
.card-button:hover {
  /*下划线*/
  text-decoration: underline;
  border: 1px solid #409eff;
}
.text {
  font-size: 14px;
}
.item {
  margin-bottom: 18px;
}

.box-card {
  border: 1px solid rgba(0, 0, 0, 0.1);
  /*圆角*/
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  transition: all 0.3s;
  cursor: pointer;
  /*背景色*/
  background-color: #f5f5f7;
}
.box-card:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  border: 1px solid rgb(64, 158, 255, 0.8);
  /*color: rgb(64, 158, 255, 0.8);*/
}
.card-title {
  font-size: 20px;
  /*font-weight: bold;*/
  color: #092441;
  display: flex;
  /*justify-content: center;*/
  align-items: center;
}


/* 产品卡片 */
.spu-card {
  width: 48%;
  /*height: 75px;*/
  margin-bottom: 10px;
  margin-left: 5px;
  margin-right: 5px;
  float: left;
  background-color: rgba(197, 197, 197, 0);
}
.spu-card:hover {
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.2);
  border: 1px solid rgb(64, 158, 255, 0.8);
  /*color: rgb(64, 158, 255, 0.8);*/
}
.spu-img {
  height: 45px;
  width: 45px;
}
</style>
